export default {
	data() {
		return {
			login: {
				name:"",
				username:"",
				password:"",
				registrationCode:""
			},
			loading: false,
		};
	},
	methods:{
		defaultHandlerLogin:function(){
			this.loading = true;
			uni.request({
			    url: 'http://47.104.180.97:8080/miniApi/login', //仅为示例，并非真实接口地址。
			    data: this.login,
			    success: (res) => {
					this.loading = false;
			        console.log(res.data);
			    }
			});
		},
		BindInput:function(e){
			var dataval = e.currentTarget.dataset.val;
			this.login[dataval] = e.detail.value; 
		},
	}
}
